<template>
	<div id="statcenter-finance">
		<public-header title="财务统计"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell>
					<div slot="title" class="c-default flex">
						<i class="icon icon-peoplefill font-18 margin-5-r c-primary"></i>
						顾客
					</div>
				</van-cell>
				<van-cell title="顾客总数" :value="finance.member.total_num" class="border-0px"></van-cell>
				<van-cell title="顾客总余额" :value="`¥${finance.member.total_credit2}`" class="border-0px" v-if="0"></van-cell>
				<van-cell title="顾客总积分" :value="`¥${finance.member.total_credit1}`" class="border-0px" v-if="0"></van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title" class="c-default flex">
						<i class="icon icon-shopfill font-18 margin-5-r c-info"></i>
						商户
					</div>
				</van-cell>
				<van-cell title="商户总数" :value="finance.store.total_num" class="border-0px"></van-cell>
				<van-cell title="商户未提现金额" :value="`¥${finance.store.total_amount}`" class="border-0px"></van-cell>
				<van-cell title="商户申请提现未处理金额" :value="`¥${finance.store.total_getcash}`" class="border-0px"></van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title" class="c-default flex">
						<i class="icon icon-people3 font-18 margin-5-r c-danger"></i>
						配送员
					</div>
				</van-cell>
				<van-cell title="配送员总数" :value="finance.deliveryer.total_num" class="border-0px"></van-cell>
				<van-cell title="配送员未提现金额" :value="`¥${finance.deliveryer.total_credit2}`" class="border-0px"></van-cell>
				<van-cell title="配送员申请提现未处理金额" :value="`¥${finance.deliveryer.total_getcash}`" class="border-0px"></van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				finance: {
					member: {},
					store: {},
					deliveryer: {}
				},
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'plateform/statcenter/finance',
					autoAssign: true,
					variable: 'finance',
				})
			}
		},
		mounted() {
			this.onLoad()
		}
	}
</script>

<style>
	#statcenter-finance .van-cell .van-cell__title{
		color: #7a7a7a;
	}
</style>


